	import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue';
import json from '@/json';
import mixPulldownRefresh from '@/components/mix-pulldown-refresh/mix-pulldown-refresh';
import mixLoadMore from '@/components/mix-load-more/mix-load-more';
let windowWidth = 0,
	scrollTimer = false,
	tabBar;
export default {
	components: {
		mixPulldownRefresh,
		mixLoadMore,
		uniNavBar
	},
	data() {
		return {
			trues:true,
			tabCurrentIndex: 0, //当前选项卡索引
			scrollLeft: 0, //顶部选项卡左滑距离
			enableScroll: true,
			tabBars: [],
			pageNo:1
		};
	},

	async onLoad() {
		// 获取屏幕宽度
		windowWidth = uni.getSystemInfoSync().windowWidth;
		
		this.loadTabbars();
	},
	onReady() {},
	methods: {
		statusChange(index) {
			switch (index) {
				case 1:
					return '通知';
					break;
				case 2:
					return '公告';
					break;
		
				case 3:
					return '预案';
					break;
				default:
					return '通知';
			}
		},
		ontrueGetList() {
			console.log('加载了通知/公告，可以把网络请求放这里');
			// 获取屏幕宽度
			windowWidth = uni.getSystemInfoSync().windowWidth;
			this.loadTabbars();
		},
		
		/**
		 * 数据处理方法在vue和nvue中通用，可以直接用mixin混合
		 * 这里直接写的
		 * mixin使用方法看index.nuve
		 */
		//获取分类
		loadTabbars() {
			let tabList = json.tabList1;
			tabList.forEach(item => {
				item.newsList = [];
				item.loadMoreStatus = 0; //加载更多 0加载前，1加载中，2没有更多了
				item.refreshing = 0;
			});
			this.tabBars = tabList;
			this.loadNewsList('add');
		},
		//新闻列表
		loadNewsList(type) {
			let tabItem = this.tabBars[this.tabCurrentIndex];

			//type add 加载更多 refresh下拉刷新
			if (type === 'add') {
				if (tabItem.loadMoreStatus === 2) {
					return;
				}
				tabItem.loadMoreStatus = 1;
			}






			var obj={type: this.tabCurrentIndex+1,pageNo:this.pageNo}
			
           this.$http
           	.get('/interface/rest/http/xlwb/xlgc-wb-xcx-tzgg-lb.htm', {params: obj})
           	.then(res => {
           		console.log(res);
           		if (res.data.msgState == 1) {
           			var list = res.data.list;
           			console.log('list');
           			console.log(list);
					
						
						if (type === 'refresh') {
							tabItem.newsList = []; //刷新前清空数组
						}
						list.forEach(item => {
							tabItem.newsList.push(item);
						});
						//下拉刷新 关闭刷新动画
						if (type === 'refresh') {
							this.$refs.mixPulldownRefresh && this.$refs.mixPulldownRefresh.endPulldownRefresh();



							if(res.data.totalPages===0||res.data.curPage===res.data.totalPages){
							tabItem.loadMoreStatus =2	
							}else tabItem.loadMoreStatus = 0;
							
						}
						//上滑加载 处理状态
						if (type === 'add') {
							if(res.data.totalPages===0||res.data.curPage===res.data.totalPages){
							tabItem.loadMoreStatus =2	
							}
						}
					
           		} else {
           			uni.showLoading({
           				title: '请求失败'
           			});
           		}
           	})
           	.catch(err => {
           		console.log(err);
           	});
			
		},
		//详情
		navToDetails(item) {
			console.log(item)
			let data = item;
			let url =  'notificationDetail';

			uni.navigateTo({
				url: `/pages/notificationDetail/${url}?data=${JSON.stringify(data)}`
			});
		},

		//下拉刷新
		onPulldownReresh() {
			this.loadNewsList('refresh');
		},
		//上滑加载
		loadMore() {
			this.loadNewsList('add');
		},
		//设置scroll-view是否允许滚动，在小程序里下拉刷新时避免列表可以滑动
		setEnableScroll(enable) {
			if (this.enableScroll !== enable) {
				this.enableScroll = enable;
			}
		},

		//tab切换
		async changeTab(e) {
			this.pageNo=1
			if (scrollTimer) {
				//多次切换只执行最后一次
				clearTimeout(scrollTimer);
				scrollTimer = false;
			}
			let index = e;
			//e=number为点击切换，e=object为swiper滑动切换
			if (typeof e === 'object') {
				index = e.detail.current;
			}
			if (typeof tabBar !== 'object') {
				tabBar = await this.getElSize('nav-bar');
			}
			//计算宽度相关
			let tabBarScrollLeft = tabBar.scrollLeft;
			let width = 0;
			let nowWidth = 0;
			//获取可滑动总宽度
			for (let i = 0; i <= index; i++) {
				let result = await this.getElSize('tab' + i);
				width += result.width;
				if (i === index) {
					nowWidth = result.width;
				}
			}
			if (typeof e === 'number') {
				//点击切换时先切换再滚动tabbar，避免同时切换视觉错位
				this.tabCurrentIndex = index;
			}
			//延迟300ms,等待swiper动画结束再修改tabbar
			scrollTimer = setTimeout(() => {
				if (width - nowWidth / 2 > windowWidth / 2) {
					//如果当前项越过中心点，将其放在屏幕中心
					this.scrollLeft = width - nowWidth / 2 - windowWidth / 2;
				} else {
					this.scrollLeft = 0;
				}
				if (typeof e === 'object') {
					this.tabCurrentIndex = index;
				}
				this.tabCurrentIndex = index;
              
				//第一次切换tab，动画结束后需要加载数据
				let tabItem = this.tabBars[this.tabCurrentIndex];
				this.loadNewsList('add');
				// if (this.tabCurrentIndex !== 0 && tabItem.loaded !== true) {
				// 	this.loadNewsList('add');
				// 	tabItem.loaded = true;
				// }
			}, 300);
		},
		//获得元素的size
		getElSize(id) {
			return new Promise((res, rej) => {
				let el = uni
					.createSelectorQuery()
					.in(this)
					.select('#' + id);
				el.fields(
					{
						size: true,
						scrollOffset: true,
						rect: true
					},
					data => {
						res(data);
					}
				).exec();
			});
		}
	}
};
